<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 
                v-text-field(outlined hide-details v-model="username" label="用户名")
            v-spacer
            v-btn(icon @click="search")
                v-icon mdi-magnify
            v-spacer
            v-btn(icon @click="refresh")
                v-icon mdi-refresh
        v-container
            v-row 
                v-col(cols="12" sm="12" lg="12")
                    span {{address}}
                v-col(cols="12" sm="12" lg="4")
                    v-overflow-btn(v-model="token"
                                            :items="tokens"
                                            label="Token"
                                            editable
                                            dense)
                    //- v-text-field(outlined hide-details v-model="token" label="Token")
                v-col(cols="12" sm="12" lg="4")
                    v-overflow-btn(v-model="direction"
                                            :items="directions"
                                            label="转入/转出"
                                            editable
                                            dense)
                v-col(cols="12" sm="12" lg="4")
                    v-text-field( v-model="tokenbalance" 
                    required
                    outlined
                    :append-icon="sunbalance ? 'mdi-eye' : 'mdi-eye-off'" 
                    @click:append="sunbalance = !sunbalance"
                    label="余额")
                
        v-list
            historylistitem(
                v-for="tx in trc10transfers"
                :key="tx.transactionHash"
                :from="tx.transferFromAddress"
                :to="tx.transferToAddress"
                :tokenname="gettokenname(tx.tokenName)"
                :amount="tx.amount/gettokenprecision"
                :time="tx.timestamp"
                :hash="tx.transactionHash"
            )

        v-pagination(
            v-model="page"
            class="my-4"
            :length="pages"
            :total-visible="7"
        )
</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    
    components: {
        historylistitem: () => import('@/views/trx/trxComponents/historylistitem'),
    },
    data: () => ({
    sunbalance:false,
    precision:6,
    tokens:[{text:"全部",value:""},{text:"OKT",value:"1003019"},{text:"ODKT",value:"1003018"},{text:"BNS",value:"1003495"},{text:"TRX",value:"_"}],
    token:"",
    directions:[{text:"全部",value:""},{text:"转入",value:"in"},{text:"转出",value:"out"}],
    // address:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r",
    trc10transfers:[],
    total:20,
    page:1,
    pagesize:20,
    // start:0,
    direction:"",
    accountinfo:{},
    username:"",
    tokenprecisionmap:{},
    }),
    computed: {
    isLogin:get('user/islogin'),
    address:get('user/trxhistoryaddress'),
    exbotisexpire:get('user/exbotisexpire'),
    tokenbalance(){
        if (this.accountinfo[this.token]){
            return this.accountinfo[this.token]/this.gettokenprecision;
        }else{
            return 0
        }
    },
    pages(){
        let pages= parseInt (this.total/this.pagesize);
        if (pages>1){
            return pages
        }
        return 1
    },
    start(){
        return (this.page-1)*this.pagesize;
    },
    gettokenprecision(){
        let precesion=Math.pow(10,parseInt(this.tokenprecisionmap[Number(this.token)]));
        if (this.sunbalance){
            return 1
        }else if (precesion){
            return precesion
        }else{
            return 1000000
        }
    },
    
    
    },
    methods:{
        // 获取余额精度 
        getprecision(){
            if (this.token=="_"){
                return 
            }
            if (this.token){
                let tokenprecisionmap=this.tokenprecisionmap;
                api.GetTrc10TokenInfo(this.token).then(res=>{
                    for (var i = 0; i < res.data.length; i++) { 
                        tokenprecisionmap[String(res.data[i].tokenID)]= parseInt(res.data[i].precision);
                        tokenprecisionmap[Number(res.data[i].tokenID)]= parseInt(res.data[i].precision);
                    }
                    this.tokenprecisionmap=tokenprecisionmap;
                })
                
            }
        },
        search(){
            if (this.username){
                api.GetAddressByUsername({username:this.username}).then(res=>{
                    if (res.code==200){
                        this.$store.dispatch("user/update_trxhistoryaddress",res.data);
                    }else{
                        this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                    }
                })
            }else{
                this.$store.dispatch("msglog/push_msg",{message:"请输入用户名",type:"error"})
            }
            
        },
        gettokenname(tokenid){
            let tokenmap={};
            for (var i = 0; i < this.tokens.length; i++) { 
                tokenmap[String(this.tokens[i].value)]= this.tokens[i].text;
            }
            if (tokenmap[tokenid]){
                return tokenmap[tokenid]
            }
            return tokenid
        },  
        refresh(){
            this.page=1;
            let params = {
                "address": this.address,
                "count": true,
                "sort": "-timestamp",
                "limit": this.pagesize,
                "start": this.start,
                "relatedAddress":this.address,
            }
            if (this.token){
                params["token"]=this.token;
                this.getprecision();
            }
            if (this.direction){
                params["direction"]=this.direction;
            }
            api.GetTrc10History(params).then(res=>{
                if (res.total>0){
                    this.total=res.total;
                    this.trc10transfers=res.data;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:"获取失败",type:"error"})
                }
            })
            api.GetTrxTokenBalance(this.address).then(res=>{
                let accountinfo ={};
                for (var i = 0; i < res.tokenBalances.length; i++) { 
                    accountinfo[String(res.tokenBalances[i].name)]= res.tokenBalances[i].balance;
                }
                this.accountinfo=accountinfo;    
            })
            api.GetUserNameByAddress({address:this.address}).then(res=>{
                if (res.code==200){
                    this.username=res.data;
                }
            })
            this.getprecision();
        },
        getpagedata(){
            let params = {
                "address": this.address,
                "conunt": true,
                "sort": "-timestamp",
                "limit": this.pagesize,
                "start": this.start,
                "relatedAddress":this.address,
            }
            if (this.token){
                params["token"]=this.token;
            }
            if (this.direction){
                params["direction"]=this.direction;
            }
            api.GetTrc10History(params).then(res=>{
                if (res.total>0){
                    this.total=res.total;
                    this.trc10transfers=res.data;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:"获取失败",type:"error"})
                }
            })
        },
        
    },
    watch:{
        page(){
            this.getpagedata();
        }
    }
}
</script>